const toElement = elementOrQuery => (
  typeof elementOrQuery === 'string'
    ? document.querySelector(elementOrQuery)
    : elementOrQuery
);

function getElementY(elementOrQuery) {
  return window.pageYOffset + toElement(elementOrQuery).getBoundingClientRect().top;
}

/**
 * Helper: Плавный скрол к заданому
 */
export default (container, element, duration) => {
  const containerElement = toElement(container);
  // const startingY = window.pageYOffset;
  const containerY = getElementY(container);
  const startingY = document.body.scrollHeight - containerY < window.innerHeight
    ? document.body.scrollHeight - window.innerHeight
    : containerY;
  const elementY = getElementY(element);
  // If element is close to page's bottom then window will scroll only to some position above the element.
  const targetY = document.body.scrollHeight - elementY < window.innerHeight
    ? document.body.scrollHeight - window.innerHeight
    : elementY;
  const diff = targetY - startingY;
  // Easing function: easeInOutCubic
  // From: https://gist.github.com/gre/1650294
  // eslint-disable-next-line no-mixed-operators
  const easing = t => (t < 0.5 ? 4 * t * t * t : (t - 1) * (((2 * t) - 2) * ((2 * t) - 2)) + 1);
  let start;

  if (!diff) return;
  // Bootstrap our animation - it will get called right before next frame shall be rendered.
  window.requestAnimationFrame(function step(timestamp) {
    if (!start) start = timestamp;
    // Elapsed miliseconds since start of scrolling.
    const time = timestamp - start;
    // Get percent of completion in range [0, 1].
    let percent = Math.min(time / duration, 1);
    // Apply the easing.
    // It can cause bad-looking slow frames in browser performance tool, so be careful.
    percent = easing(percent);

    containerElement.scrollTo(0, -targetY + (diff * percent));

    // Proceed with animation as long as we wanted it to.
    if (time < duration) {
      window.requestAnimationFrame(step);
    }
  });
};



