<template>
  <div class="the-token-search-result-new">
    <template v-if="isLoading">
      <div class="the-token-search-result-new__message">
        Searching Tokens on
        <span class="the-token-search-result-new__link">EtherScan.io</span>...
      </div>
      <div class="the-token-search-result-new__wrapper">
        <div class="the-token-search-result-new__search-list">
          <e-preloader/>
        </div>
      </div>
    </template>

    <template v-else-if="tokensToDisplay.length === 0">
      <div class="the-token-search-result-new__message">
        No Tokens Found on
        <span class="the-token-search-result-new__link">EtherScan.io:</span>
      </div>
      <div class="the-token-search-result-new__recomend">
        We recomend you 3 most popular tokens
      </div>
      <div class="the-token-search-result-new__rec">
        <e-token-search-result-item
          v-for="item in recommendedTokens"
          :key="item.contract"
          v-bind="item"
          @click="handleSelect"
        />
      </div>
    </template>

    <template v-else-if="tokensToDisplay.length > 0">
      <div class="the-token-search-result-new__message">
        {{tokensToDisplay.length > 0 ? `Found ${tokensToDisplay.length}` : 'We found 0'}} Tokens on
        <span class="the-token-search-result-new__link">EtherScan.io:</span>
      </div>
      <div class="the-token-search-result-new__wrapper">
        <div
          class="the-token-search-result-new__search-list"
          :class="mobile ? 'the-token-search-result-new__search-list_height' : ''"
        >
          <e-token-search-result-item
            v-for="item in tokensToDisplay"
            :key="item.contract"
            v-bind="item"
            @click="handleSelect"
          />
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import debounce from 'lodash/debounce';
import { searchTokens } from '@/api/backend';
import { searchTokenMixin } from '@/util/mixins';
import { ASYNC_STATES } from '@/util/constants';

import EPreloader from '@/components/EPreloader';
import ETokenSearchResultItem from '@/components/ETokenSearchResultItem';

const handleSearchUpdate = debounce(async function handler(nextSearchString) {
  let tokens;
  this.requestState = ASYNC_STATES.PENDING;
  try {
    tokens = await searchTokens(nextSearchString);
  } catch (err) {
    this.requestState = ASYNC_STATES.ERROR;
  }
  this.requestState = ASYNC_STATES.SUCCESS;
  this.tokensToDisplay = tokens.slice(0, 20);
}, 100, { maxWait: 250 });

export default {
  name: 'the-token-search-result-new',

  props: {
    searchString: {
      type: String,
      default: '',
    },

    mobile: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      tokensToDisplay: [],
      requestState: ASYNC_STATES.INITIAL,
    };
  },

  computed: {
    ...mapGetters({
      recommendedTokens: 'tokens/recommendedTokens',
    }),

    isLoading() {
      return this.requestState === ASYNC_STATES.PENDING;
    },
  },

  watch: {
    searchString: {
      immediate: true,
      handler: 'handleSearchUpdate',
    },
  },

  created() {
    this.$store.dispatch('tokens/loadTokens');
  },

  methods: {
    handleSearchUpdate,

    handleSelect(item) {
      this.$emit('select', item);
    },
  },

  mixins: [searchTokenMixin],

  components: { ETokenSearchResultItem, EPreloader },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-token-search-result-new {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;

  &__wrapper {
    display: flex;
  }

  &__search-list {
    background-color: #fff;
    min-height: 110px;
    max-height: 215px;
    width: 100%;
    overflow-y: auto;
    position: relative;
    border-radius: 0 0 4px 4px;

    &::-webkit-scrollbar {
      position: relative;
      width: 3px;
      left: -6px;
    }

    &::-webkit-scrollbar-track {
      background-color: #fff;
      border-radius: 1px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #979797;
      border-radius: 1px;
    }
  }

  &__search-list_height {
    max-height: 100%;
  }

  &__message {
    flex: 0 0 auto;
    height: 37px;
    background-color: #f5f5f5;
    border-radius: 4px 4px 0 0;
    color: #000;
    font-size: 10px;
    letter-spacing: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__link {
    margin-left: 5px;
    color: #1F2EE9;
  }

  &__recomend {
    color: rgba(0, 0, 0, 0.4);
    font-size: 13px;
    font-weight: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    background-color: #f5f5f5;
  }
}
</style>



