import { mapGetters } from 'vuex';
import openLink from '@/util/openLink';

import EIcon from '@/components/EIcon';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';

import iconSearch from '@/icons/e-icon_search.svg';
import iconClose from '@/icons/e-icon_round-close.svg';
import iconLink from '@/icons/e-icon__link2.svg';

export default {

  data() {
    return {
      searchValue: '',
      isNotFocused: true,
      searchHasDomFocus: false,
    };
  },

  icons: {
    iconSearch,
    iconClose,
    iconLink,
  },

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
      activeToken: 'tokens/activeToken',
    }),

    tokenSearchSelected() {
      const token = this.activeToken;
      if (token) {
        return {
          contract: token.contract,
          shortName: token.shortName,
          name: token.name,
        };
      }
      return null;
    },
  },

  methods: {
    handleTokenSelect(item) {
      this.$router.push({ name: 'token', params: { token: item.contract } });
      this.searchValue = '';
      this.unfocusSearch();
    },

    etherscanClick(e) {
      e.stopPropagation();
      openLink(`https://etherscan.io/token/${this.tokenSearchSelected.contract}`);
    },

    toShortenedAddress(a) {
      return `${a.slice(0, 4)}…${a.slice(-4)}`;
    },

    handleClickOutside() {
      this.searchValue = '';
      this.unfocusSearch();
    },

    focusSearch() {
      const searchEl = this.$refs.search;
      if (searchEl) {
        searchEl.focus();
      }
      this.isNotFocused = false;
    },

    unfocusSearch() {
      const searchEl = this.$refs.search;
      if (searchEl) {
        searchEl.blur();
      }
      this.isNotFocused = true;
    },

    handleFocusSearch() {
      // this.isNotFocused = false;
    },

    handleBlurSearch() {
      // this.isNotFocused = true;
      // this.searchValue = '';
    },

    clearSearch() {
      this.focusSearch();
      this.searchValue = '';
    },
  },

  components: {
    EIcon,
    TheTokenSearchResultNew,
  },
};
</script>

