<template>
  <router-link :to="{ name: 'index' }" class="the-logo-inner">
    <div class="the-logo-inner__ethermium">
      <e-icon :src="iconLogo"/>
    </div>
    <div class="the-logo-inner__body" :class="{'hide-volume': isHideVolume}">
      <div class="the-logo-inner__text">Ether<span class="the-logo-inner__variety">Mium</span></div>
      <div class="the-logo-inner__vol" v-if="!isHideVolume && isVolumeReady">
        <e-big-number :value="volumeTotal" :decimals="ETH_DECIMALS" :decimalPlaces="3"/> ETH Volume
      </div>

      <div class="the-logo-inner__icon-pending" v-if="!isHideVolume && !isVolumeReady">
        <e-icon :src="iconDottedAnimation" />
      </div>
    </div>
  </router-link>
</template>

<script>
import { mapState } from 'vuex';

import iconLogo from '@/icons/e-icon_ethermium-logo.svg';
import iconDottedAnimation from '@/icons/e-icon_dotted-animation.svg';

import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';

export default {
  name: 'the-logo-inner',

  props: {
    isHideVolume: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      iconLogo,
      iconDottedAnimation,
      isVolumeReady: false,
    };
  },

  computed: {
    ...mapState({
      volumeTotal: state => state.trades.volumeTotal,
    }),
  },

  watch: {
    volumeTotal() {
      if (this.volumeTotal && !this.isVolumeReady) {
        setTimeout(() => { this.isVolumeReady = true; }, 1000);
      }
    },
  },

  created() {
    this.$store.dispatch('trades/listenToTradeVolumesChange');
    this.$store.dispatch('trades/loadVolumes');
  },

  beforeDestroy() {
    this.$store.dispatch('trades/stopListeningToTradeVolumesChanges');
  },

  components: {
    EIcon,
    EBigNumber,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-logo-inner {
  position: relative;
  margin-top: 4px;
  box-sizing: border-box;
  height: 70px;
  border-radius: 3px;
  padding-top: 14px;
  padding-left: 18px;
  display: block;
  text-decoration: none;

  @include r(1200) {
    margin-top: 0;
    padding-top: 12px;
  }

  @include h(750) {
    margin-top: 0;
    padding-top: 12px;
  }

  &__chart {
    height: 44px;
    width: 212px;

    @include r(1200) {
      width: 161px;
    }

    @include h(750) {
      width: 161px;
    }
  }

  &__ethermium {
    width: 32px;
    margin-right: 12px;
    margin-top: -4px;
  }

  &__vol {
    font-size: 10px;
    font-weight: 600;
    color: #AFB8BE;
  }

  &__body {
    padding-left: 44px;
    padding-top: 4px;
    font-weight: 600;
    color: #0D105B;

    &.hide-volume {
      padding-top: 12px;
    }
  }

  &__text {
    font-size: 24px;
    font-weight: 600;
    line-height: 1;
  }

  &__icon-pending {
    width: 24px;
    height: 3px;
    margin: 6px 0 0 2px;
  }
}

